      <h4 class="fw-bold">Evacuation meant life</h4>
      <p class="fw-bold">
        The situation of the Polish Armed Forces in the USSR - as General Władysław Anders' Army was officially called - was difficult from the beginning. It improved somewhat as the Germans achieved further success at the front. After they were repelled from Moscow, Stalin decided to get rid of the army that was inconvenient for him.
