      <p>
        Eighteen-year-old Artur Gruber, one of the thousands of Poles imprisoned in the Soviet gulag after September 17, 1939, recalled that when he stood before a military medical board, he looked like a skeleton clad in rags. "An mercilessly dirty skeleton," he added. A young doctor examining him, at one point asked his older colleague: "Why do they all have such red dots one next to the other?"
      </p>
      <p>
        This doctor must have come from Great Britain, because if he had been one of the exiles or labor camps in Russia, he would have known that these were the traces of lice bites ... This is how most of the recruits reporting to General Anders' Army recruiting points looked like. Their situation in the military camp improved only slightly. They still suffered from hunger and cold, as the Soviet allies were sending neither sufficient rations nor supplies. The situation was not improved by the dislocation of the Army to Uzbekistan, Kyrgyzstan and Kazakhstan at the end of December 1941. Although the soldiers and their families were no longer exposed to frost, infectious diseases quickly began to decimate them. The conditions of accommodation, especially food, also deteriorated significantly. And the latter, as it turned out, was to be even more limited.
      </p>

      <h4 class="fw-bold">

In the snare
</h4>
      <p>
        During the meeting of the Commander-in-Chief, General Władysław Sikorski, with Józef Stalin at the beginning of December 1941, it was established that 25,000 Polish soldiers - airmen and specialists of various weapons, who were missing from the Polish Armed Forces in the West, would be evacuated from the Soviet Union. In addition to how to evacuate these people to Great Britain and the Middle East, General Anders faced another, even more serious problem. Namely, the Soviets began to pressure him to send the 5th Infantry Division to the front. It was the only reasonably armed division in Anders' Army. The rest of the units, neither the Russians nor the British, wanted to equip, and any talks on this subject had no effect. After unpleasant experiences in France in 1940, Poles defended themselves against the division of the Army and sending its individual divisions to the front. So General Anders, with the consent of General Sikorski, decisively refused.
      </p>
      <p>
       During the meeting of the Commander-in-Chief, General Władysław Sikorski, with Józef Stalin at the beginning of December 1941, it was established that 25,000 Polish soldiers - airmen and specialists of various weapons, who were missing from the Polish Armed Forces in the West, would be evacuated from the Soviet Union. In addition to how to evacuate these people to Great Britain and the Middle East, General Anders faced another, even more serious problem. Namely, the Soviets began to pressure him to send the 5th Infantry Division to the front. It was the only reasonably armed division in Anders' Army. The rest of the units, neither the Russians nor the British, wanted to equip, and any talks on this subject had no effect. After unpleasant experiences in France in 1940, Poles defended themselves against the division of the Army and sending its individual divisions to the front. So General Anders, with the consent of General Sikorski, decisively refused.
      </p>

      <h4 class="fw-bold">Don't look back</h4>
      <p>
        
Several dozen minutes after the end of the conversation with Stalin, General Anders met with NKVD general Georgi Sergeyovich Zhukov, the representative of the Soviet government for the formation of Polish troops in the USSR. To the surprise of the Polish commander, Zhukov had with him a set of documents for the evacuation of about 30,000 soldiers and 10,000 members of their families, that is, the surplus of people for whom the Soviets did not provide food. As Professor
 Edward Kospath-Pawłowski emphasizes, "This supports the thesis that Stalin sought to evacuate Polish troops from the USSR, and the evacuation itself was to begin several dozen hours later [from the meeting with Anders - PK]." The first evacuation orders were given by General Anders in Moscow, on March 19. The evacuation was to pass through the base in Krasnowodsk, the commander of which the general appointed Lieutenant Colonel Zygmunt Berling. The first shipment left from here to Iran's Pehlewi on March 24. Let us give our voice to Artur Gruber, mentioned at the beginning: “On board the Soviet tanker we left the country of unfulfilled hopes and very sad, gray people without regrets. We left a cemetery behind. Epidemics of spotted and abdominal typhus and bloody dysentery took a heavy toll among exhausted Poles. Some say that there are more of them buried there than at Monte Cassino. On the second day in the evening we saw the shores of Iran. Pehlewi harbor was dimly lit by electric light. After disembarking, hearing the Polish speech of the bustling paramedics receiving the sick from our transport, I felt like I was in Poland, among my own, safe ”.

      </p>
      <p>
        This evacuation lasted until April 5. The soldiers of the 8th, 9th and 10th Infantry Divisions, an armored brigade, regiments of lancers and sappers, companies of railway engineers and most of the Army Organizational Center, surplus of the 6th and 7th Infantry Divisions reached Iran (then called Persia). In addition, all Polish airmen and sailors who were found in Russia among the living were embarked on Pehlewi.


      </p>

      <h4 class="fw-bold">We all go out</h4>
      <p>
        It quickly turned out that this was not the end of the evacuation of Polish troops from the Soviet Union. On May 14, Vyacheslav Molotov in a note to the Polish ambassador to Moscow, Stanisław Kot, stressed that he did not see the possibility of further enlisting in the Polish Army, since it was to be sent outside the USSR. This statement was the aftermath of Molotov's visit to London, during which - without the Poles knowing - the British proposed to move the Polish troops remaining in Russia to the Middle East. An advocate of such a solution was also General Anders, who sent reports to the staff of the Commander-in-Chief about the deteriorating supply situation of his troops. General Sikorski, on the other hand, still linked his hopes to the presence of Polish soldiers on the Eastern Front. As it turned out, the decision about the fate of the Polish Army in the USSR was made without the knowledge of both generals and regardless of their opinion on the subject. At the end of June 1942, Molotov sent a message to London that the Soviet government agreed to surrender the three Polish divisions remaining on Soviet territory. Churchill thanked Stalin for the Polish soldiers whom he "so kindly offered." The British prime minister had something to be happy about, as the Poles could provide him with cover for Iranian and Iraqi refineries and oil pipelines, which were strategic for the activities carried out on the African front. General Anders learned about these decisions with almost a month delay, and on July 26 he received an official letter in which it was emphasized that "The USSR Government agrees to meet the efforts of the Commander of the Polish Army in the USSR [...] to evacuate Polish troops from the USSR to the Middle East. and does not intend to put any obstacles in the immediate implementation of the evacuation. " On July 31, the Polish-Soviet military commission determined that about 70,000 military and civilians would leave Krasnowodzko to Pehlewi during August. The evacuation protocol signed by General Anders contained, inter alia, the sentence that "The Polish government does not consider it possible to use troops formed in the USSR on the Soviet-German front." This wording was used by the Soviets to actually suspend further recruitment to the Polish Army. In both evacuation operations, 116,543 people, including 78,631 military, were evacuated to the Middle East. For people who in this way could leave the "inhuman land", General Anders became a man of providence and, as Professor Czesław Brzoza accurately emphasized: "... the soldiers treated their commander as Moses, who led them out of the" house of slavery ".


      </p>

