      <h4 class="fw-bold">L'evacuazione significava vita</h4>
      <p class="fw-bold">
        La situazione delle forze armate polacche in URSS - come veniva ufficialmente chiamata l'esercito del generale Władysław Anders - era difficile fin dall'inizio. È migliorato un po 'quando i tedeschi hanno ottenuto ulteriori successi al fronte. Dopo che furono respinti da Mosca, Stalin decise di sbarazzarsi dell'esercito che era scomodo per lui.
