      <p>
        Questo medico doveva provenire dalla Gran Bretagna, perché se fosse stato uno degli esiliati o dei campi di lavoro in Russia, avrebbe saputo che queste erano tracce di morsi di pidocchi ... Questo è il modo in cui la maggior parte delle reclute che riferiscono al generale Anders «Sembravano i punti di reclutamento dell'esercito. La loro situazione nel campo militare è migliorata solo leggermente. Soffrivano ancora la fame e il freddo, poiché gli alleati sovietici non inviavano né razioni né rifornimenti sufficienti. La situazione non fu migliorata dalla dislocazione dell'esercito in Uzbekistan, Kirghizistan e Kazakistan alla fine di dicembre 1941. Sebbene i soldati e le loro famiglie non fossero più esposti al gelo, le malattie infettive iniziarono rapidamente a decimarli. Anche le condizioni di alloggio, in particolare il cibo, sono peggiorate in modo significativo. E quest'ultimo, come si è scoperto, doveva essere ancora più limitato.
      </p>

      <h4 class="fw-bold">

Nella trappola
</h4>
      <p>
        Durante l'incontro del comandante in capo, generale Władysław Sikorski, con Józef Stalin all'inizio di dicembre 1941, fu stabilito che 25.000 soldati polacchi - aviatori e specialisti di varie armi, che mancavano dalle forze armate polacche nel Ovest, sarebbe stato evacuato dall'Unione Sovietica. Oltre a come evacuare queste persone in Gran Bretagna e in Medio Oriente, il generale Anders ha dovuto affrontare un altro problema ancora più serio. Vale a dire, i sovietici iniziarono a fare pressioni su di lui per inviare la 5a divisione di fanteria al fronte. Era l'unica divisione ragionevolmente armata dell'esercito di Anders. Il resto delle unità, né i russi né gli inglesi, volevano equipaggiarsi, e qualsiasi colloquio su questo argomento non ebbe alcun effetto. Dopo spiacevoli esperienze in Francia nel 1940, i polacchi si difesero contro la divisione dell'esercito e mandarono al fronte le sue singole divisioni. Così il generale Anders, con il consenso del generale Sikorski, rifiutò decisamente.
      </p>
      <p>
       Ricevette una risposta il 6 marzo 1942. Il capo dei rifornimenti dell'Armata Rossa, il generale Andrei Khrulev, annunciò una drastica riduzione delle razioni di cibo a 26.000. Ciò significava fame per molti dei soldati e quasi tutti i civili riuniti dall'esercito. Il generale Anders decise immediatamente di intervenire con Stalin, sostenendo che ciò violava l'accordo negoziato dal generale Sikorski. Stalin ha ricevuto Anders in udienza il 18 marzo e ha accettato di aumentare la razione di cibo a 44.000. Sarebbe sufficiente per tre divisioni e un reggimento di riserva. Il resto delle unità doveva essere evacuato in Medio Oriente.
      </p>

      <h4 class="fw-bold">Non guardare indietro</h4>
      <p>
        
Diverse decine di minuti dopo la fine della conversazione con Stalin, il generale Anders ha incontrato il generale dell'NKVD Georgi Sergeyovich Zhukov, il rappresentante del governo sovietico per la formazione delle truppe polacche in URSS. Con sorpresa del comandante polacco, Zhukov aveva con sé una serie di documenti per l'evacuazione di circa 30.000 soldati e 10.000 membri delle loro famiglie, cioè l'eccedenza di persone per le quali i sovietici non fornivano cibo. Come professore
 Edward Kospath-Pawłowski sottolinea: "Questo supporta la tesi secondo cui Stalin ha cercato di evacuare le truppe polacche dall'URSS, e l'evacuazione stessa doveva iniziare diverse dozzine di ore dopo [dall'incontro con Anders - PK]". I primi ordini di evacuazione furono dati dal generale Anders a Mosca, il 19 marzo. L'evacuazione doveva passare per la base di Krasnowodsk, il cui comandante il generale nominò il tenente colonnello Zygmunt Berling. La prima spedizione è partita da qui alla Pehlewi iraniana il 24 marzo. Diamo la nostra voce ad Artur Gruber, citato all'inizio: “A bordo della petroliera sovietica abbiamo lasciato il Paese di speranze insoddisfatte e di persone grigie molto tristi senza rimpianti. Abbiamo lasciato un cimitero alle spalle. Le epidemie di tifo maculato e addominale e dissenteria sanguinolenta hanno avuto un pesante tributo tra i polacchi esausti. Alcuni dicono che ce ne siano più seppelliti lì che a Monte Cassino. Il secondo giorno di sera abbiamo visto le coste dell'Iran. Il porto di Pehlewi era scarsamente illuminato dalla luce elettrica. Dopo lo sbarco, ascoltando il discorso polacco dei frenetici paramedici che ricevevano i malati dal nostro trasporto, mi sono sentito come se fossi in Polonia, tra i miei, al sicuro ”.


      </p>
      <p>
        Questa evacuazione durò fino al 5 aprile. I soldati dell'8a, 9a e 10a divisione di fanteria, una brigata corazzata, reggimenti di lancieri e zappatori, compagnie di ingegneri ferroviari e la maggior parte del Centro Organizzativo dell'Esercito, raggiunti i surplus della 6a e 7a Divisione di fanteria. Iran (allora chiamato Persia). Inoltre, tutti gli aviatori ei marinai polacchi trovati in Russia tra i vivi furono imbarcati su Pehlewi.

      </p>

      <h4 class="fw-bold">Usciamo tutti</h4>
      <p>
        Si è presto scoperto che questa non era la fine dell'evacuazione delle truppe polacche dall'Unione Sovietica. Il 14 maggio, Vyacheslav Molotov in una nota all'ambasciatore polacco a Mosca, Stanisław Kot, ha sottolineato di non vedere la possibilità di arruolarsi ulteriormente nell'esercito polacco, poiché doveva essere inviato al di fuori dell'URSS. Questa dichiarazione è stata all'indomani della visita di Molotov a Londra, durante la quale - all'insaputa dei polacchi - gli inglesi hanno proposto di spostare le truppe polacche rimaste in Russia in Medio Oriente. Un sostenitore di tale soluzione era anche il generale Anders, che ha inviato rapporti allo staff del comandante in capo sul deterioramento della situazione di approvvigionamento delle sue truppe. Il generale Sikorski, d'altra parte, ancora lega le sue speranze alla presenza di soldati polacchi sul fronte orientale. Come si è scoperto, la decisione sul destino dell'esercito polacco in URSS è stata presa all'insaputa di entrambi i generali e indipendentemente dalla loro opinione sull'argomento. Alla fine di giugno 1942, Molotov ha inviato un messaggio a Londra che il governo sovietico ha accettato di cedere le tre divisioni polacche rimaste sul territorio sovietico. Churchill ha ringraziato Stalin per i soldati polacchi che ha "offerto così gentilmente". Il premier britannico aveva qualcosa di cui essere contento, in quanto i polacchi potevano fornirgli una copertura per raffinerie e oleodotti iraniani e iracheni, strategici per le attività svolte sul fronte africano. Il generale Anders venne a conoscenza di queste decisioni con quasi un mese di ritardo e il 26 luglio ricevette una lettera ufficiale in cui si sottolineava che "Il governo dell'URSS accetta di soddisfare gli sforzi del comandante dell'esercito polacco in URSS [... ] per evacuare le truppe polacche dall'URSS in Medio Oriente. e non intende porre alcun ostacolo nell'immediata attuazione dell'evacuazione. "Il 31 luglio, la commissione militare polacco-sovietica ha stabilito che circa 70.000 militari e civili avrebbero lasciato Krasnowodzko a Pehlewi nel mese di agosto. Il protocollo di evacuazione firmato dal generale Anders conteneva, tra l'altro, la frase che "Il governo polacco non ritiene possibile utilizzare le truppe formate in URSS sul fronte sovietico-tedesco". Questa formulazione fu usata dai sovietici per sospendere effettivamente l'ulteriore reclutamento nell'esercito polacco. In entrambe le operazioni di evacuazione, 116.543 persone, di cui 78.631 militari, sono state evacuate in Medio Oriente. Per le persone che in questo modo potevano lasciare la "terra disumana", il generale Anders divenne un uomo di provvidenza e, come sottolineò accuratamente il professor Czesław Brzoza: "... i soldati trattavano il loro comandante come Mosè, che li condusse fuori di casa" di schiavitù ".


      </p>

